.product-detail {
  display: flex;
  flex-direction: column;
}

.product-detail__album,
.album {
  position: relative;
}


.album__image-main {
  object-fit: cover;
  aspect-ratio: 1/1;
  display: none;
  width: 100%;
}

.album__image-list,
.image-list {
  display: flex;
  white-space: nowrap;
  overflow: scroll;
  scroll-snap-type: x mandatory;
}

.image-list__image {
  object-fit: cover;
  width: 100vw;
  height: 100vw;
  aspect-ratio: 1/1;
  scroll-snap-align: center;
}

.album__amount-image {
  position: absolute;
  bottom: 4vw;
  right: 3vw;
  color: var(--color-text);
}

.album__amount-image::before {
  content: "1";
}

[data-amount="1"]::before {
  content: "1";
}

[data-amount="2"]::before {
  content: "2";
}

[data-amount="3"]::before {
  content: "3";
}

.album__btn-next-image {
  position: absolute;
  top: 50vw;
  right: var(--edge-mobile);
  width: 2rem;
  height: 2rem;
  border-radius: 100%;
  text-align: center;
  color: white;
  background-color: rgba(128, 128, 128, 0.4);
}

.album__btn-next-image--disable {
  display: none;
}

.album__btn-prev-image {
  position: absolute;
  top: 50vw;
  left: var(--edge-mobile);
  width: 2rem;
  height: 2rem;
  border-radius: 100%;
  text-align: center;
  color: white;
  background-color: rgba(128, 128, 128, 0.4);
}

.album__btn-prev-image--disable {
  display: none;
}

.product-detail__detail,
.detail {
  padding: var(--edge-mobile);
  gap: 1vw;
  background-color: #fff;
  color: var(--color-text);
  font-size: small;
}

.detail__status,
.status {
  display: flex;
  justify-content: space-between;
}

.status__add-to-favorite {}

.status__price {}

.detail__name {
  margin: var(--edge-mobile) 0;
}


.detail__title,
.title {}

.title__ratting {}

.title__amount-in-stock {}

.detail__name {}


.detail__add-item,
.add-item {
  position: fixed;
  bottom: 0;
  left: 0;
  display: flex;
  width: 100%;
  background-color: var(--bgc-2);
  z-index: 1;
  height: 10vh;
  justify-content: space-between;
  color: white;
  font-size: larger;
  text-align: center;
  align-self: center;
  padding: var(--edge-mobile)
}

.add-item__decrease {
  order: 1;
  background-color: inherit;
  width: 20vw;
  color: inherit;
  border: 1px solid #000;
  align-self: center;
}

.add-item__amount {
  order: 2;
  font-size: large;
  color: inherit;
  border: 1px solid #000;
  align-self: center;
}

.add-item__increase {
  order: 3;
  background-color: inherit;
  width: 20vw;
  color: inherit;
  border: 1px solid #000;
  align-self: center;
}

.add-item__add-to-cart {
  order: 4;
  background-color: inherit;
  color: inherit;
  border: 1px solid #000;
  align-self: center;
  padding: 1rem;
}